
  .container {
    display: grid;
    grid-template-columns: 1fr;
  }
  
  main {
    display: flex;
    justify-content: center;
  }
  
  main img {
    margin-top: 55px;
  }
  
  .content1 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .content1 img {
    width: 500px;
  }
  .content1 img {
    transition: 0.5s;
  }
  .content1 img:hover {
    opacity: 0.5;
  }
  
  .content2 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .content3 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  .content3 img {
    transition: 0.5s;
    width: 1000px;
  }
  .content3 img:hover {
    opacity: 0.5;
    width: 1000px;
  }



@media only screen and (min-width: 414px) and (max-width: 767px),
  (min-width: 412px) and (max-width: 767px),
  (min-width: 390px) and (max-width: 767px),
  (min-width: 428px) and (max-width: 767px),
  (min-width: 384px) and (max-width: 767px),
  (min-width: 360px) and (max-width: 767px) {

    .container{
      width: 100%;
      display: grid;
      grid-template-columns: 1fr;
    }
    main {
      display: flex;
      justify-content: center;
    }
    
    main img {
      margin-top: 55px;
      width: 350px;
    }
    
    .content1 {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    
    .content1 img {
      width: 350px;
    }
    .content1 img {
      transition: 0.5s;
    }
    .content1 img:hover {
      opacity: 0.5;
    }
    
    .content2 {
      width: 0;
      height: 0;
    }
    
    .content3 {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 20px;
    }
    .content3 img {
      transition: 0.5s;
      margin-bottom: 50px;
      width: 350px;
    }
    .content3 img:hover {
      opacity: 0.5;
      width: 350px;
    }
}
